@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.background {
    width: 300px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    text-align: center;
    font-size: 20px;
    margin-top: 1rem;
    padding: 0.3rem;
}
.activeContainer {
    background-image: url(/mill-game/static/media/activeElement.59920f28.svg);
    color: #FF8B37;
}
.inActiveContainer {
    background-image: url(/mill-game/static/media/inActiveElement.2ef8e0ae.svg);
    color: #D0D0D8;
}
.nextContainer {
    background-image: url(/mill-game/static/media/nextElement.2ef8e0ae.svg);
    color: #1C1C21;
}
.button1 {
    float: left;
    width: 25rem;
    margin: 2rem 0 0 2rem;
    background-image: url(/mill-game/static/media/btnInActive.b99b78fd.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    text-align: center;
    line-height: 72px;
    font-size: 20px;
    font-family: Inter, sans-serif;
    cursor: pointer;
    outline: none;
    transition: all 0.2s;
}

.button1:hover {
    background-image: url(/mill-game/static/media/btnHover.81e27c02.svg);
}

.button1:active {
    background-image: url(/mill-game/static/media/btnActive.e7f978d3.svg);
}

.false {
    background-image: url(/mill-game/static/media/btnFalse.bbcda7ee.svg);
}
.true {
    background-image: url(/mill-game/static/media/btnTrue.7e859aee.svg);
}
.score {
    font-size: 32px;
    font-weight: 600;
    color: #1C1C21;
    opacity: 0.5;
}
.hand {
    background-image: url(/mill-game/static/media/hand.a2492ddf.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    width: 400px;
    height: 400px;
}
.price {
    color: #1C1C21;
    font-size: 56px;
    font-weight: 600;
}
.startBtn {
    cursor: pointer;
}
.container {
    display: flex;
}
.board {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    min-height: 100vh;
    background-color: #E5E5E5;
}
/*=====question side=====*/
.question {
    padding: 2rem 0 0 3rem;
    min-height: 30vh;
    max-width: calc(100vw - 100px);
}
/*=====answers side=====*/
.answers {
    padding-bottom: 2rem;
    max-width: calc(100vw - 200px);
}
/*=====menu side=====*/
.menu {
    position: absolute;
    top: 0;
    right: 0;
    background-color: white;
    height: 100vh;
    padding: 0 0 1rem 0;
}
.btnMenu {
    position: fixed;
    background-image: url(/mill-game/static/media/btnMenu.22e84c27.svg);
}
.btnClose {
    /*position: fixed;*/
    margin: 1rem;
    background-image: url(/mill-game/static/media/btnClose.9e20d061.svg);
}
.hide {
    display: none;
}
.bckgnd {
    top: 1rem;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    color: #1C1C21;
    cursor: pointer;
}
body {
    position: relative;
    overflow-x: hidden;
}
body:before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    border-bottom: 100vh solid rgba(255,243,235,1);
    border-left: 100vw solid transparent;
    z-index: -1;
}
.main {
    background-color: transparent;
    margin-top: 5rem;
}
.img {
    background-image: url(/mill-game/static/media/hand.a2492ddf.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    width: 400px;
    height: 400px;
}
.mainText {
    position: static;
    width: 624px;
    height: 130px;
    font-family: Inter, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 56px;
    line-height: 116%;
    display: flex;
    align-items: center;
    color: #1C1C21;
    flex: none;
    order: 0;
    align-self: center;
    flex-grow: 0;
    margin: 2rem 0;
}
.startBtn {
    width: 296px;
    height: 64px;
    border-radius: 12px;
    background-color: #FF8B37;
    outline: none;
    border: none;
    transition: all 0.2s;
}
.textBtn {
    font-weight: bold;
    color: white;
}
.startBtn:hover {
    background-color: #FFAC70;
}
.startBtn:active {
    background-color: #E87928;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Inter,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

